.header .navbar {
	/*background: #f8f8f8 url("../../assets/images/patterns/pattern.jpg");*/
	background: #fff;
	box-sizing: border-box;
	box-shadow:0 6px 12px rgba(0,0,0,.175);
	padding: 0.25rem 1rem;
	border-top: 2px solid #135ABE;
}

.header .logo {
    width: 200px;
    margin: 0;
    /*margin-top: -4px;*/
}

.navbar-light .navbar-nav .nav-link {
	color: hsla(215, 82%, 41%, .8);
}

.navbar-light .navbar-nav .nav-link:hover {
    color: #135ABE;
}
.card.service {
    height: 100%;
}
.card.service h5.card-title {
    position: relative;
    margin-bottom: 30px;
}
.card.service h5.card-title:after {
    position: absolute;
    bottom: -15px;
    left: 0;
    right: 0;
    content: '';
    width: 30px;
    height: 1px;
    background: #3364FF;
    margin: 0 auto;
}
.card.service .icon {
    display: block;
    margin-bottom: 30px;
}
.card.service .icon i {
    line-height: 1.0;
    color: #3e64ff;
}
.card.service .card-text {
    color: #777;
}
.hire-me {
    background-image: url(/static/media/bg_1.f7de43c6.jpg);
    /*background: #3e64ff;*/
    z-index: 0;
    position: relative;
    padding: 120px;
    margin: 0;
    color: #fff;
}
.hire-me h2 > span {
	color: #9FF569;
}
.hire-me .overlay {
	position: absolute;
	content: '';
	background: #3e64ff;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	opacity: .7;
}
.hire-me a.btn {
	border-radius: 100px;
}
.footer {
    font-size: 16px;
    background: #000000;
    padding: 7em 0;
    z-index: 0;
}
.footer .footer-logo {
    text-transform: uppercase;
    letter-spacing: .1em;
}
.footer .footer-widget h2 {
    font-weight: normal;
    color: #fff;
    margin-bottom: 40px;
    font-size: 22px;
    font-weight: 600;
}
.footer .footer-widget ul li {
    margin-bottom: 10px;
}
.footer .footer-widget ul li a {
    color: rgba(255, 255, 255, 0.6);
}
.footer .footer-widget ul li a span {
    color: rgba(255, 255, 255, 0.6);
}
.footer .footer-widget .btn-primary {
    background: #fff !important;
    border: 2px solid #fff !important;
}
.footer .footer-widget .btn-primary:hover {
    background: #fff;
    border: 2px solid #fff !important;
}
.footer p {
    color: rgba(255, 255, 255, 0.7);
}
.footer a {
    color: rgba(255, 255, 255, 0.7);
}
.footer a:hover {
    color: #fff;
}
.footer .heading-2 {
    font-size: 17px;
    font-weight: 400;
    color: #000000;
}
.footer-social li {
    list-style: none;
    margin: 0 10px 0 0;
    display: inline-block;
}
.footer-social li a {
    height: 38px;
    width: 38px;
    display: block;
    float: left;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 50%;
    position: relative;
}
.footer-social li a span {
    position: absolute;
    font-size: 18px;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}
.footer-social li a:hover {
    color: #fff;
}
.footer-small-nav>li {
    display: inline-block;
}
.footer-small-nav>li a {
    margin: 0 10px 10px 0;
}
.footer-small-nav>li a:hover, .footer-small-nav>li a:focus {
    color: #3e64ff;
}
